[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.header{position:fixed;top:0;left:0;width:100%;height:80px;background-color:var(--element-bg);box-shadow:0px 0px 6px var(--shadow);z-index:1}.header .header__container{max-width:1440px;margin:0 auto;display:flex;align-items:center;justify-content:space-between}.header .header__container .header__title{color:var(--text);margin:0;padding:1em 2em;font-family:'Nunito Sans', sans-serif;font-size:20px;font-weight:800;text-decoration:none;color:var(--text)}
