[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.country{max-width:1440px;margin:80px auto 0;display:flex;flex-direction:column}.country .country__info-wrapper{margin:3em;display:flex}.country .country__info-wrapper .country__spinner{display:inline-block;width:80px;height:80px;margin:50px auto 0}.country .country__info-wrapper .country__spinner:after{content:" ";display:block;width:64px;height:64px;border-radius:50%;border:10px solid var(--text);border-color:var(--text) transparent var(--text) transparent;animation:spin .4s ease-in-out infinite}@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.country .country__info-wrapper .country__image{width:520px;height:340px;object-fit:fill}.country .country__info-wrapper .country__main-wrapper{margin:0 100px 0}.country .country__info-wrapper .country__main-wrapper .country__title{margin:0;color:var(--text);font-family:'Nunito Sans', sans-serif;font-size:20px;font-weight:800;text-decoration:none;color:var(--text)}.country .country__info-wrapper .country__main-wrapper .country__text{margin:.1em 0;font-size:18px;line-height:24px;font-weight:300;color:var(--text)}.country .country__info-wrapper .country__main-wrapper .country__text-wrapper{height:140px;width:600px;display:flex;flex-direction:column;flex-wrap:wrap;margin:1.5em 0}.country .country__info-wrapper .country__main-wrapper .country__border-countries{display:flex;align-items:center}.country .country__info-wrapper .country__main-wrapper .country__border-countries .border-countries__list{width:400px;margin-left:20px;list-style:none;display:flex;flex-wrap:wrap}.country .country__info-wrapper .country__main-wrapper .country__border-countries .border-countries__list .border-countries__item{padding:.5em;margin:.25em;color:var(--text);background-color:var(--element-bg);outline:1px solid var(--shadow);border-radius:6px}.country .country__info-wrapper .country__main-wrapper .country__border-countries .border-countries__list .border-countries__item:hover{cursor:pointer}.country .country__button-back{margin:4em 3.5em 0;padding:1em;width:140px;border:none;color:var(--text);box-shadow:0px 0px 6px var(--shadow);background-color:var(--element-bg);outline:1px solid var(--shadow);border-radius:6px}.country .country__button-back:hover{cursor:pointer;opacity:.7}
