[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.countries-item{width:264px;box-shadow:0px 0px 10px var(--shadow);background-color:var(--element-bg);outline:1px solid var(--shadow);border-radius:6px}.countries-item .countries-item__image{width:100%;height:160px;outline:1px solid var(--shadow);border-radius:6px 6px 0 0}.countries-item .countries-item__image:hover{cursor:pointer}.countries-item .countries-item__title{text-decoration:none;font-size:20px;line-height:20px;font-weight:600;color:var(--text)}.countries-item .countries-item__title>p{margin:.75em 1em}.countries-item .countries-item__info{padding:.25em 1.1em;font-size:16px;line-height:16px;font-weight:300;color:var(--text)}.countries-item .countries-item__info:last-of-type{padding-bottom:3em}
