[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.theme-switcher{padding:1em;margin:0 2em;display:flex;align-items:center;justify-content:space-between;border:none;background-color:rgba(255,255,255,0);border-radius:6px}.theme-switcher:hover{background-color:var(--background);cursor:pointer}.theme-switcher p{margin-left:.5em;font-size:18px;line-height:18px;font-weight:600;color:var(--text)}
