[data-theme='light']{--background: hsl(0, 0%, 98%);--element-bg: hsl(0, 0%, 100%);--text: hsl(200, 15%, 8%);--shadow: hsla(0, 0%, 52%, 0.3);--input: hsl(0, 0%, 52%)}[data-theme='dark']{--background: hsl(207, 26%, 17%);--element-bg: hsl(209, 23%, 22%);--text: hsl(0, 0%, 100%);--input: hsla(0, 0%, 85%, 0.9);--shadow: rgba(22, 22, 22, 0.2)}.custom-filter{position:relative;margin:2em;padding:.75em}.custom-filter .custom-filter__button{width:180px;padding:14px;background-color:var(--element-bg);font-family:'Nunito Sans', sans-serif;font-size:18px;font-weight:400;color:var(--text);outline:1px solid var(--shadow);border-radius:6px}.custom-filter .custom-filter__button:hover{cursor:pointer}.custom-filter .custom-filter__list{position:absolute;width:180px;top:68px;left:12px;list-style:none;background-color:var(--element-bg);outline:1px solid var(--shadow);border-radius:6px}.custom-filter .custom-filter__item{padding-left:1em;line-height:36px;transition:0;font-family:'Nunito Sans', sans-serif;font-size:18px;font-weight:400;color:var(--text)}.custom-filter .custom-filter__item:hover{cursor:pointer;background-color:var(--background)}
